





import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import ClientEditor from "@/components/misc/ClientEditor.vue";
import { Overwrite, IUpdateClientArgs } from "@/shared";
import { adminModule, routerModule } from "@/store/modules";
import { IClient } from "@/shared";

@Component({
  components: {
    ClientEditor
  }
})
export default class EditClientView extends Mixins() {
  get getClientId(): string {
    return routerModule.params.clientId;
  }

  get clientData() {
    return adminModule.clientbyID(this.getClientId);
  }

  async onSubmit(client: IClient) {
    const resp = await adminModule.updateClient(client);
    if (resp && resp.payload) {
      this.$router.replace({
        path: `/admin/client/${resp.payload.client.id}`
      });
    }
  }
}
